import { Injectable } from '@angular/core';
import { HttpClient } from "@angular/common/http";
import { environment } from '../../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class DeviceService {

  constructor(private httpClient: HttpClient) { }

  getDevices(InterfaceID) {
    if (typeof (InterfaceID) == 'undefined') {
      return this.httpClient.get(environment.API_URL + 'Devices');
    } else {
      return this.httpClient.get(environment.API_URL + 'Interfaces/' + InterfaceID + '/Devices');
    }
  }

  getDevice(DeviceID) {
    return this.httpClient.get(`${environment.API_URL + 'Devices'}/${DeviceID}`);
  }

  // Optional callback object allows a dialog 'loadData' function to be called post operation
  createDevice(Device, callback = null) {
    return this.httpClient.post(`${environment.API_URL + 'Devices'}`, JSON.stringify(Device))
      .subscribe(
        (val) => {
          console.log("POST call successful value: ", val);
        },
        response => {
          console.log("POST call in error", response);
          if (callback != null) callback.loadData();
        },
        () => {
          console.log("The POST observable is now completed.");
          if (callback != null) callback.loadData();
        });
  }

  // Optional callback object allows a dialog 'loadData' function to be called post operation
  updateDevice(DeviceId, Device, callback = null) {
    return this.httpClient.put(`${environment.API_URL + 'Devices'}/${DeviceId}`, JSON.stringify(Device))
      .subscribe(
        (val) => {
          console.log("PUT call successful value: ", val);
        },
        response => {
          console.log("PUT call in error", response);
          if (callback != null) callback.loadData();
        },
        () => {
          console.log("The PUT observable is now completed.");
          if (callback != null) callback.loadData();
        });
  }

  // Optional callback object allows a dialog 'loadData' function to be called post operation
  deleteDevice(DeviceId, callback=null) {
    return this.httpClient.delete(`${environment.API_URL + 'Devices'}/${DeviceId}`)
      .subscribe(
        (val) => {
          console.log("DELETE call successful value: ", val);
        },
        response => {
          console.log("DELETE call in error", response);
          if (callback != null) callback.loadData();
        },
        () => {
          console.log("The DELETE observable is now completed.");
          if (callback != null) callback.loadData();
        });
  }
}
