import { Injectable } from '@angular/core';
import { HttpClient } from "@angular/common/http";
import { environment } from '../../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class RoleService {

  constructor(private httpClient: HttpClient) { }

  getRoles() {
    return this.httpClient.get(environment.API_URL + 'Roles')
  }

  getRole(RoleId) {
    return this.httpClient.get(`${environment.API_URL + 'Roles'}/${RoleId}`);
  }

  createRole(Role) {
    return this.httpClient.post(`${environment.API_URL + 'Roles'}`, `${Role}`);
  }

  updateRole(RoleId, Role) {
    return this.httpClient.post(`${environment.API_URL + 'Roles'}/${RoleId}`, `${Role}`);
  }

  deleteRole(RoleId) {
    return this.httpClient.delete(`${environment.API_URL + 'Roles'}/${RoleId}`);
  }
}
