import { Injectable } from '@angular/core';
import { HttpClient } from "@angular/common/http";
import { environment } from '../../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class UnitService {

  constructor(private httpClient: HttpClient) { }

  getUnits() {
    return this.httpClient.get(environment.API_URL + 'Units')
  }

  getUnit(UnitId) {
    return this.httpClient.get(`${environment.API_URL + 'Units'}/${UnitId}`);
  }

  createUnit(Unit) {
    return this.httpClient.post(`${environment.API_URL + 'Units'}`, `${Unit}`);
  }

  updateUnit(UnitId, Unit) {
    return this.httpClient.post(`${environment.API_URL + 'Units'}/${UnitId}`, `${Unit}`);
  }

  deleteUnit(UnitId) {
    return this.httpClient.delete(`${environment.API_URL + 'Units'}/${UnitId}`);
  }
}
