import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import { MatSnackBar } from '@angular/material';
import { TimerPlanService } from "../timer-plan.service";
import { TimerPlan, TimerPlansMessage } from "../timer-plan";

@Component({
  selector: 'dmz-timer-plan-list',
  templateUrl: './timer-plan-list.component.html',
  styleUrls: ['./timer-plan-list.component.css']
})
export class TimerPlanListComponent implements OnInit {

  TimerPlans: TimerPlan[] = [];
  tableColumns: string[] = ['TimerPlanID', 'Name', 'Active'];
  selectedRow = 0;
  selectedRowNumber = 0;

  constructor(
    private TimerPlanService: TimerPlanService,
    private snackBar: MatSnackBar,
    private location: Location)
  { }

  goBack() {
    this.location.back();
    console.log('goBack()...');
  }

  ngOnInit() {
    this.TimerPlanService.getTimerPlans().subscribe((TimerPlansMsg: TimerPlansMessage) => {
      this.TimerPlans = TimerPlansMsg.TimerPlans;
      console.log(this.TimerPlans);
      this.snackBar.open(TimerPlansMsg.Count + ' Timer Plan' + (this.TimerPlans.length > 1 ? 's' : '') + ' loaded. ', '', { duration: 3000 });
      return this.TimerPlans;
    }, (error) => {
      if (error.status == 404) {
        this.snackBar.open('No Timer Plans found. ', '', { duration: 5000 });
      }
      else {
        this.snackBar.open(error.statusText, '', { duration: 10000 });
      }
    })
  }
}
