import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LayoutComponent } from "src/app/dashboard/layout/layout.component";
import { LayoutListComponent } from "src/app/dashboard/layout-list/layout-list.component"; 


const dashboardRoutes: Routes = [
  { path: 'Layouts', component: LayoutListComponent },
  { path: 'Layouts/:id', component: LayoutComponent }
];

@NgModule({
  imports: [RouterModule.forChild(dashboardRoutes)],
  exports: [RouterModule]
})
export class DashboardRoutingModule { }
