import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import { MatDialog, MatDialogConfig } from '@angular/material';
import { MatSnackBar } from '@angular/material';
import { UnitService } from "../unit.service";
import { Unit, UnitsMessage } from "../unit";
import { UnitComponent } from '../unit/unit.component';
import { ConfirmationDialogComponent } from 'src/app/system/confirmation-dialog/confirmation-dialog.component';

@Component({
  selector: 'dmz-unit-list',
  templateUrl: './unit-list.component.html',
  styleUrls: ['./unit-list.component.css']
})
export class UnitListComponent implements OnInit {

  Units: Unit[] = [];
  tableColumns: string[] = ['UnitID', 'Name', 'Minimum', 'Maximum', 'IconList', 'TextLabels'];
  selectedRow: Unit;
  selectedRowNumber = 0;
  dialogConfig = new MatDialogConfig();

  constructor(private unitService: UnitService,
    private snackBar: MatSnackBar,
    private location: Location,
    private dialog: MatDialog,
    private deleteDialog: MatDialog) { }

  goBack() {
    this.location.back();
    console.log('goBack()...');
  }

  ngOnInit() {
    this.dialogConfig.autoFocus = true;
    this.dialogConfig.width = "80%";

    this.unitService.getUnits().subscribe((UnitsMsg: UnitsMessage) => {
      if (UnitsMsg != null) {
        this.Units = UnitsMsg.Units;
        this.snackBar.open(UnitsMsg.Count + ' Unit' + (this.Units.length > 1 ? 's' : '') + ' loaded. ', '', { duration: 3000 });
        console.log(this.Units);
      }
      else this.snackBar.open('No Units returned. ', '', { duration: 5000 });
      return this.Units;
    }, (error) => {
      this.snackBar.open(error.statusText, '', { duration: 10000 });
    })
  }

  onNew() {
    this.dialogConfig.data = undefined;
    this.dialog.open(UnitComponent, this.dialogConfig);
  }

  onEdit() {
    this.dialogConfig.data = this.selectedRow;
    this.dialog.open(UnitComponent, this.dialogConfig);
  }

  onDelete() {
    const dialogRef = this.deleteDialog.open(ConfirmationDialogComponent, {
      width: '75%',
      data: "Please confirm deletion of unit '" + this.selectedRow.Name + "'?"
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        debugger;
        this.unitService.deleteUnit(this.selectedRow.UnitID);
      }
    });
  }
}
