export class Device {
  DeviceID: number;
  InterfaceID: number;
  Name: string;
  ExternalID: string;
  Timestamp: string;
  constructor(DeviceID: number = -1, InterfaceID: number = -1, Name: string = "", ExternalID: string = "", Timestamp: string = "") {
    this.DeviceID = DeviceID;
    this.InterfaceID = InterfaceID;
    this.Name = Name;
    this.ExternalID = ExternalID;
    this.Timestamp = Timestamp;
  }
}

export class DeviceMessage {
  Count: number;
  Device: Device;
}

export class DevicesMessage {
  Count: number;
  Devices: Device[];
}

