export class Interface {
  InterfaceID: number;
  Name: string;
  Script: string;
  Configuration: string;
  Notifiable: boolean;
  Active: boolean;
  constructor(InterfaceID: number = -1, Name: string = "", Script: string = "", Configuration: string = "", Notifiable: boolean = false, Active: boolean = false) {
    this.InterfaceID = InterfaceID;
    this.Name = Name;
    this.Script = Script;
    this.Configuration = Configuration;
    this.Notifiable = Notifiable;
    this.Active = Active;
  }
}

export class InterfaceMessage {
  Count: number;
  Interface: Interface;
}

export class InterfacesMessage {
  Count: number;
  Interfaces: Interface[];
}
